import React from 'react';
import { graphql } from "gatsby";
import '../scss/main.scss';
import Sidebar from '../components/Sidebar';
import SidebarButton from '../components/SidebarButton';
import Link from 'gatsby-link';
import Footer from '../components/Footer';

export default function Template({
  data, // this prop will be injected by the GraphQL query below.
}) {
  const { markdownRemark } = data // data.markdownRemark holds our post data
  const { frontmatter, html } = markdownRemark
  return (
    <div className="content">
      <SidebarButton />
      <Sidebar />
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="tile tile--primary u-m-top u-m-bottom--lg">
              <Link className="u-text--secondary" to="./">Home / </Link>
              {
                frontmatter.parent !== null
                ? <Link className="u-text--secondary" to={frontmatter.parentPath}>{frontmatter.parent} / </Link>
                : ''
              }
              <strong className="u-text--primary">{frontmatter.title}</strong>
              <h1 className="u-m-bottom--sm">{frontmatter.title}</h1>
            </div>
          </div>
        </div>
        <div className="row">

          <div className="col-12">
            <div
              className="blog-post-content"
              dangerouslySetInnerHTML={{ __html: html }}
            />
          </div>

          <Footer />

        </div>
      </div>
    </div>
  )
}

export const pageQuery = graphql`
  query($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        path
        title
        parent
        parentPath
      }
    }
  }
`
